import { Component, OnInit } from '@angular/core';
import { VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-tax',
  templateUrl: './tax.component.html',
  styleUrls: ['./tax.component.scss'],
})
export class TaxComponent implements OnInit {
  addTax: any = {};
  taxList: any = [];
  observableTaxs: any;

  constructor(
    public lang: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    private vhComponent: VhComponent,
    private vhQuery: VhQuery
  ) {}

  ngOnInit() {
    this.getData();
  }
  ngOnDestroy() {
    this.observableTaxs.unsubscribe();
  }
  /**
   * get danh sách thuế mới nhất
   */
  getData() {
    this.observableTaxs = this.vhQueryCafe
      .getObservableLocalTaxs()
      .subscribe((localTaxs: any) => {
        this.taxList = localTaxs;
      });
  }

  /**
   * kiểm tra giá trị thuế nhập vào hợp lệ hay ko
   */
  checkValue() {
    if (
      parseFloat(this.addTax.value) < 0 ||
      parseFloat(this.addTax.value) > 100
    ) {
      document.getElementById('tax-value-add')['value'] = 0;
      this.vhComponent.showToast(
        2000,
        this.lang.translate('The tax value must be <=100'),
        'alert-toast'
      );
    }
    if (
      parseFloat(this.editTax.value) < 0 ||
      parseFloat(this.editTax.value) > 100
    ) {
      document.getElementById('tax-value-edit')['value'] = 0;
      this.vhComponent.showToast(
        2000,
        this.lang.translate('The tax value must be <=100'),
        'alert-toast'
      );
    }
  }

  /**
   * tạo thuế lên DB
   */
  creatTax() {
    if (this.addTax.name != '' && parseFloat(this.addTax.value) >= 0) {
      this.vhQueryCafe.addTax(this.addTax).then(
        (res) => {
          this.vhComponent.showToast(
            2000,
            this.lang.translate('Tax has been added successfully'),
            'success-toast'
          );
          this.closeAdd();
        },
        (error) => {
          this.vhComponent.showToast(
            2000,
            this.lang.translate('Tax has been added failed'),
            'alert-toast'
          );
        }
      );
    } else {
      this.vhComponent.showToast(
        2000,
        this.lang.translate('Please enter tax name, tax value'),
        'alert-toast'
      );
    }
  }

  /**
   * xóa thuế
   */
  deleteTax(item) {
    if (!item.default) {
      this.vhComponent
        .alertConfirm(
          this.lang.translate('Confirm'),
          '',
          this.lang.translate('Are you sure to delete this tax?'),
          'OK',
          this.lang.translate('Cancel')
        )
        .then(() => {
          this.vhComponent.showLoading('', 'transparent-loading').then(() => {
            this.vhQueryCafe.deleteTax(item._id).then((bool) => {
              if (bool) {
                this.vhComponent.hideLoading(0).then(() => {
                  this.vhComponent.showToast(
                    2000,
                    this.lang.translate('Tax has been deleted successfully'),
                    'success-toast'
                  );
                  this.getData();
                });
              } else {
                this.vhComponent
                  .hideLoading(0)
                  .then(() =>
                    this.vhComponent.showToast(
                      2000,
                      this.lang.translate("Can't delete this tax"),
                      'alert-toast'
                    )
                  );
              }
            });
          });
        })
        .catch(() => {});
    } else {
      this.vhComponent.showToast(
        2000,
        this.lang.translate("Can't delete this default tax"),
        'alert-toast'
      );
    }
  }

  /**
   * bắt sk nhấn lưu thuế ở modal cập nhật thuế
   */
  update() {
    if (this.editTax.name != '' && parseFloat(this.editTax.value) >= 0)
      this.vhQueryCafe.updateTax(this.editTax._id, this.editTax).then(() => {
        this.vhComponent.showToast(
          1500,
          `${this.lang.translate('Tax')} ${this.lang.translate(
            'has been updated successfully'
          )}`,
          'success-toast'
        );
        this.closeEdit();
      });
    else {
      this.vhComponent.showToast(
        2000,
        this.lang.translate('Please enter tax name, tax value'),
        'alert-toast'
      );
    }
  }

  /**
   * cập nhật trường default cho thuế đươc chọn, update lại default = false cho các thuế khác
   */
  setDefault(tax) {
    let promise = new Array();
    this.taxList
      .filter((fillter) => fillter != tax)
      .forEach((element, index) => {
        element.default = false;
        promise[index] = this.vhQueryCafe.updateTax(element._id, {
          default: false,
          status: true,
        });
      });
    const index = this.taxList.findIndex((find) => find == tax);
    if (index) {
      this.taxList[index].default = true;
      promise.push(
        this.vhQueryCafe.updateTax(this.taxList[index]._id, {
          default: true,
          status: true,
        })
      );
    }
    this.vhQuery.asyncPromiseAll(promise).then(
      (array) => {
        if (array) {
          this.vhComponent.showToast(
            1500,
            this.lang.translate('Tax') +
              ' ' +
              this.lang.translate('has been set default successfully'),
            'success-toast'
          );
        }
      },
      (error) => {
        console.log(error);
      }
    );

  }

  /**
   * cập nhật trường status cho thuế được chọn
   * @param item 
   * @param value 
   */
  updateToggle(item, value) {
    if (item.default) item.status = true;
    else {
      item.status = value;
      this.vhQueryCafe.updateTax(item._id, item);
    }
  }

  /*----Add tax----*/
  visible_add = false; //biến cờ trạng thái show/hide popup thêm danh mục
  /**
   * mở modal add thuế
   */
  openAdd(): void {
    this.visible_add = true;
    this.addTax = { name: '', value: '' };
  }
  /**
   * đóng modal add thuế
   */
  closeAdd(): void {
    this.visible_add = false;
    this.getData();
  }

  /** ----Edit category product----- */
  visible_detail = false;
  editTax: any = {};
  /**
   * mở modal cập nhật thuế
   */
  openEdit(item): void {
    this.visible_detail = true;
    this.editTax = { ...item };
  }
  /**
   * đóng modal cập nhật thuế
   */
  closeEdit(): void {
    this.visible_detail = false;
    this.getData();
  }
}
